@import "./src/common/Components/styles/styles";

.mainBlock {
  @extend .particles;
  min-height: 100vh;
  text-align: center;
  background: var(--color-background-melon);
  padding: 3rem 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;


  .container {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 1200px;
    width: 100%;


    .mainBlock__photo {
      border-radius: 50%;
      margin-bottom: 1.5rem;
      box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.3);
      transition: box-shadow 500ms;

      &:hover {
        box-shadow: 0 10px 50px 0 var(--color-pumpkin);
      }
    }


    .profilePhoto {
      max-width: 300px;
      width: 100%;
      border-radius: 50%;
      box-shadow: 0 .5rem 1rem rgba(0, 0, 0, .15);
      padding: .5rem;
      background-color: var(--color-white);
    }
  }

  .mainBlock__text {
    font-weight: 700;
    font-size: 2.5rem;
    text-align: center;
    line-height: 1.5;
    margin-bottom: 1.5rem;

  }

  .location {
    font-size: 2rem;
    margin-bottom: 3rem;
  }
}


.scrollDown {
  margin-top: 100px;
  --color: white;
  --sizeX: 30px;
  --sizeY: 50px;
  position: relative;
  width: var(--sizeX);
  height: var(--sizeY);
  margin-left: var(sizeX / 2);
  border: calc(var(--sizeX) / 10) solid var(--color);
  border-radius: 50px;
  box-sizing: border-box;
  margin-bottom: 16px;
}

.scrollDown::before {
  content: "";
  position: absolute;
  bottom: 30px;
  left: 50%;
  width: 6px;
  height: 6px;
  margin-left: -3px;
  background-color: var(--color);
  border-radius: 100%;
  animation: scrollDown-anim 2s infinite;
  box-sizing: border-box;
  box-shadow: 0px -5px 3px 1px #ffffff66;
}

@keyframes scrollDown-anim {
  0% {
    opacity: 0;
    height: 6px;
  }
  40% {
    opacity: 1;
    height: 10px;
  }
  80% {
    transform: translate(0, 20px);
    height: 10px;
    opacity: 0;
  }
  100% {
    height: 3px;
    opacity: 0;
  }
}

.chevrons {
  padding: 6px 0 0 0;
  margin-left: -3px;
  margin-top: 48px;
  width: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.chevronDown {
  margin-top: -6px;
  position: relative;
  border: solid var(--color);
  border-width: 0 3px 3px 0;
  display: inline-block;
  width: 10px;
  height: 10px;
  transform: rotate(45deg);
}

.chevronDown:nth-child(odd) {
  animation: pulse 500ms ease infinite alternate;
}

.chevronDown:nth-child(even) {
  animation: pulse 500ms ease infinite alternate 250ms;
}

@keyframes pulse {
  from {
    opacity: 0;
  }
  to {
    opacity: 0.5;
  }
}


@media (max-width: 450px) {
  .mainBlock__text {
    & p {
      font-size: 1.6rem;
    }

    & h1 {
      font-size: 2rem;
    }

  }

}




